<template>
  <div class="content">
    <mechanicItem :data="data" :isAll="isAll" :state="state" />
  </div>
</template>

<script>
import mechanicItem from "@/components/mechanicItem";
export default {
  data() {
    return {
      data: this.getRespone.mechanicJson,
      isAll: false,
      state: 1
    };
  },
  mounted(){
    this.state = this.$route.query.state
  },
  watch: {
    $route(to, from) {
      this.$router.go(0);
    }
  },
  components: {
    mechanicItem
  }
};
</script>

